
#svg__man {
  stroke: red;
  pointer-events: none;
}
#ear {
  fill: none;
  stroke: red;
  stroke-width: 2;
}
#nose__man {
  fill: #ff6a00;
}
.skin {
  fill: orange;
  stroke: red;
}
#g__nose_and_hand {
  stroke-width: 2;
}
#eye_w__man {
  fill: white;
  stroke-width: 2;
}
#eye_ball__man {
  fill: none;
  stroke: black;
  stroke-width: 4;
}
#hair__man {
  fill: brown;
  storke: gray;
}
#body__man {
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-transform: rotate(15deg);
          transform: rotate(15deg);
}
.clothes {
  fill: gray;
}
#blood {
  fill: red;
  stroke: red;
}
@-webkit-keyframes hand_dig {
from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
to {
    -webkit-transform: translateX(-10%);
            transform: translateX(-10%);
}
}
@keyframes hand_dig {
from {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
to {
    -webkit-transform: translateX(-10%);
            transform: translateX(-10%);
}
}



